import React, { useState } from "react";
import TextInput from "./FormTextInput";
import { useCookies } from "react-cookie";
import { useHistory } from "react-router-dom";
import { useSettings } from "../context/useSettings";

const ApiForm = ({ onUpdate }) => {
  const { settings } = useSettings();
  const [, setCookie] = useCookies(["htf-settings"]);
  const [baseURL, setBaseURL] = useState("");
  const history = useHistory();

  const onSubmit = (e) => {
    e.preventDefault();
    const withTrailingSlash =
      baseURL.substr(-1, 1) === "/" ? baseURL : `${baseURL}/`;
    setCookie("htf-settings", withTrailingSlash);

    onUpdate(baseURL);
    history.push("/cluedo");
  };

  return (
    <div className="file ful api-form">
      <h2>Settings</h2>
      <p>
        Om de frontend-applicatie te kunnen koppelen aan jouw Drupal setup,
        moeten we de baseUrl van je drupal hebben. Geef deze hier in om die in
        te stellen.
      </p>
      <form onSubmit={onSubmit}>
        <TextInput
          label="Drupal baseURL:"
          name="base-url"
          placeholder={settings?.baseURL || "http://example.com"}
          onChange={setBaseURL}
        />

        <input type="submit" value="Base URL instellen" className="btn" />
      </form>
    </div>
  );
};

export default ApiForm;
