import { useCallback, useState } from "react";
import { setAuthorizationHeader } from "../Helpers/setAuthorizationHeader";
import { useSettings } from "../Components/context/useSettings";

const useFetch = () => {
  const { settings } = useSettings();
  const [response, setResponse] = useState({
    data: null,
    initial: true,
    processing: false,
    error: null,
    success: false,
  });

  const initializeState = () =>
    setResponse({
      data: null,
      initial: true,
      processing: false,
      error: null,
      success: false,
    });

  const doFetch = useCallback(
    async (url, reqOptions) => {
      const abortCont = new AbortController();

      try {
        if (!settings.baseURL) {
          throw Error("No baseURL provided");
        }
        if (!url) {
          throw Error("No URL Provided");
        }

        const requestSettings = {
          method: reqOptions.method ?? "GET",
          body: reqOptions.body ? JSON.stringify(reqOptions.body) : null,
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
            Authorization: setAuthorizationHeader(reqOptions.auth),
          },
        };

        setResponse({
          data: null,
          initial: true,
          processing: true,
          success: false,
          error: null,
        });

        const request = await fetch(`${settings.baseURL}${url}`, {
          ...requestSettings,
          signal: abortCont.signal,
        });

        if (request.status !== 200) {
          throw Error(`${request.status}: ${request.statusText}`);
        }

        const reply = await request.json();

        if (!request.ok && reply.message) {
          throw Error(`${request.status}: ${reply.message}`);
        }

        setResponse({
          data: reply,
          initial: false,
          processing: false,
          error: false,
          success: true,
        });
      } catch (err) {
        if (err.name === "AbortError") {
          console.log("Fetch Aborted!");
          return;
        }

        setResponse({
          data: null,
          initial: false,
          processing: false,
          error: err.message,
          success: false,
        });
      }

      return () => abortCont.abort();
    },
    [settings]
  );

  return { response, doFetch, initializeState };
};

export default useFetch;
