import React, { useContext, useEffect, useState } from "react";
import { CluesContext } from "../Main";
import ClueDetail from "./ClueDetail";
import { NoBaseUrlMessage } from "../NoBaseUrlMessage";
import { useSettings } from "../context/useSettings";

const Clues = () => {
  const clues = useContext(CluesContext);
  const { settings } = useSettings();

  const [selectedClue, setSelected] = useState(null);
  const [suspects, setSuspects] = useState(null);
  const [rooms, setRooms] = useState();
  const [weapons, setWeapons] = useState();

  useEffect(() => {
    if (!clues.data) {
      return;
    }

    const suspects = clues.data.filter(
      (clue) => clue.type === settings.clueTypes.suspect
    );
    const rooms = clues.data.filter(
      (clue) => clue.type === settings.clueTypes.room
    );
    const weapons = clues.data.filter(
      (clue) => clue.type === settings.clueTypes.weapon
    );

    setSuspects(suspects);
    setRooms(rooms);
    setWeapons(weapons);
  }, [clues, settings]);

  return settings ? (
    <>
      {!!suspects && !!rooms && !!weapons ? (
        <div className={"full file"}>
          <h2>Aanwijzingen</h2>

          {selectedClue && (
            <div
              className="clue-detail"
              style={{ verticalAlign: "bottom", textAlign: "justify" }}
            >
              {clues.error && <div>{clues.error}</div>}
              {clues.processing && <div>Fetching Clues ...</div>}
              {selectedClue && <ClueDetail clue={selectedClue} />}
            </div>
          )}

          {clues.data && (
            <div className="clue-coloms">
              <div className="clue-colom">
                <h2>Verdachten</h2>
                <ul>
                  {suspects.map((suspect) => (
                    <li
                      key={suspect.id}
                      className={selectedClue === suspect ? "active" : ""}
                      onClick={() => setSelected(suspect)}
                    >
                      {suspect.title}
                    </li>
                  ))}
                </ul>
              </div>

              <div className="clue-colom">
                <h2>Wapens</h2>
                <ul>
                  {weapons.map((weapon) => (
                    <li
                      key={weapon.id}
                      className={selectedClue === weapon ? "active" : ""}
                      onClick={() => setSelected(weapon)}
                    >
                      {weapon.title}
                    </li>
                  ))}
                </ul>
              </div>

              <div className="clue-colom">
                <h2>Kamers</h2>
                <ul>
                  {rooms.map((room) => (
                    <li
                      key={room.id}
                      className={selectedClue === room ? "active" : ""}
                      onClick={() => setSelected(room)}
                    >
                      {room.title}
                    </li>
                  ))}
                </ul>
              </div>
            </div>
          )}
        </div>
      ) : (
        <div>
          <i
            className={"fas fa-exclamation-triangle"}
            style={{ marginRight: "10px" }}
          />
          Geen API data gevonden.
        </div>
      )}
    </>
  ) : (
    <NoBaseUrlMessage />
  );
};

export default Clues;
