import React, { useEffect, useState } from "react";
import { CluesContext } from "../Main";
import useFetch from "../../Hooks/Fetch";
import Button from "../Button";
import FormSelectInput from "./FormSelectInput";
import { useSettings } from "../context/useSettings";

const CluedoForm = ({ title, url, onSubmit, roomId, onClose }) => {
  const clues = React.useContext(CluesContext);
  const { settings } = useSettings();

  const [suspect, setSuspect] = useState(null);
  const [weapon, setWeapon] = useState(null);
  const [room, setRoom] = useState(roomId ?? null);

  const suspects = React.useMemo(
    () => clues.data.filter((clue) => clue.type === settings.clueTypes.suspect),
    [clues, settings]
  );
  const weapons = React.useMemo(
    () => clues.data.filter((clue) => clue.type === settings.clueTypes.weapon),
    [clues, settings]
  );
  const rooms = React.useMemo(
    () => clues.data.filter((clue) => clue.type === settings.clueTypes.room),
    [clues, settings]
  );

  const { response, doFetch, initializeState } = useFetch();

  const handleSubmit = (e) => {
    e.preventDefault();

    if (!suspect) {
      alert("Kies een verdachte");
      return;
    }
    if (!weapon) {
      alert("Nog geen wapen opgegeven");
      return;
    }
    if (!room) {
      alert("Nog geen kamer opgegeven");
      return;
    }

    const requestParam = {
      method: "POST",
      body: { suspect, room, weapon },
      auth: {
        user: process.env.REACT_APP_USERNAME,
        password: process.env.REACT_APP_PASSWORD,
      },
    };

    doFetch(url, requestParam);
  };

  useEffect(() => {
    if (!response.initial && response.success) {
      const logEntry = {
        suggested: {
          weapon: weapon,
          room: room,
          suspect: suspect,
        },
        response: response.data,
      };
      onSubmit(logEntry);
      initializeState();
    }
  }, [response, weapon, room, suspect, onSubmit, initializeState]);

  return (
    <div>
      <h2>{title}</h2>
      {response.error && <div>{response.error}</div>}
      <div className="container">
        <form onSubmit={handleSubmit}>
          <FormSelectInput
            name="suspect"
            values={suspects}
            placeholder="verdachte ..."
            onChange={setSuspect}
          />
          <FormSelectInput
            name="weapon"
            values={weapons}
            placeholder="wapen ..."
            onChange={setWeapon}
          />
          {!roomId && (
            <FormSelectInput
              name="room"
              values={rooms}
              placeholder="kamer ..."
              onChange={setRoom}
            />
          )}
          <div className="cluedo-btn-box">
            <Button
              type="submit"
              value={
                title === "Arrest" ? "Maak arrestatie" : "Doe een suggestie"
              }
              disabled={!response.initial}
            />
            <Button
              value="Terug"
              onClick={() => onClose()}
              disabled={!response.initial}
            />
          </div>
        </form>
      </div>
    </div>
  );
};

export default CluedoForm;
