import React, { useState } from "react";
import { FaTimes } from "react-icons/fa";
import { CluesContext } from "../Main";
import NoteEntry from "./NoteEntry";
import { useSettings } from "../context/useSettings";

const Notebook = ({ onClick, disproved }) => {
  const clues = React.useContext(CluesContext);
  const { settings } = useSettings();

  const [isShown, setShow] = useState(false);

  const toggleNotes = () => {
    setShow(!isShown);
  };

  const suspects = clues.data.filter(
    (clue) => clue.type === settings.clueTypes.suspect
  );
  const weapons = clues.data.filter(
    (clue) => clue.type === settings.clueTypes.weapon
  );
  const rooms = clues.data.filter(
    (clue) => clue.type === settings.clueTypes.room
  );

  const suspectList = suspects.map((clue) => {
    return (
      <NoteEntry
        key={clue.id}
        clue={clue}
        onClick={onClick}
        disproved={disproved.includes(parseInt(clue.id))}
      />
    );
  });
  const weaponList = weapons.map((clue) => (
    <NoteEntry
      key={clue.id}
      clue={clue}
      onClick={onClick}
      disproved={disproved.includes(parseInt(clue.id))}
    />
  ));
  const roomList = rooms.map((clue) => (
    <NoteEntry
      key={clue.id}
      clue={clue}
      onClick={onClick}
      disproved={disproved.includes(parseInt(clue.id))}
    />
  ));

  return (
    <div>
      {isShown ? (
        <div id="notebook" className={"notes"} display="none">
          <h2 className={"notes-title"}>
            Notities
            <FaTimes
              size={20}
              onClick={toggleNotes}
              style={{ cursor: "pointer" }}
            />
          </h2>
          <div className={"note"}>
            <h3>WIE?</h3>
            <ul>{suspectList}</ul>
          </div>
          <div className={"note"}>
            <h3>WAT?</h3>
            <ul>{weaponList}</ul>
          </div>
          <div className={"note"}>
            <h3>WAAR?</h3>
            <ul>{roomList}</ul>
          </div>
        </div>
      ) : (
        <button onClick={toggleNotes} className="notes-btn" value={"Notities"}>
          Notities
        </button>
      )}
    </div>
  );
};

export default Notebook;
