import React, { useEffect } from "react";
import useFetch from "../../Hooks/Fetch";
import { useSettings } from "../context/useSettings";

const CluedoStart = ({ onStart }) => {
  const { settings } = useSettings();
  const { response: gameKey, doFetch } = useFetch();

  const handleNewGame = () => {
    doFetch(settings.url.new, {
      method: "GET",
      auth: {
        user: settings.auth.username,
        password: settings.auth.password,
      },
    });
  };

  useEffect(() => {
    if (gameKey.data && gameKey.data.key) {
      onStart(gameKey.data.key);
    }
  }, [gameKey, onStart]);

  return (
    <div className={"file full"}>
      <h2>Cluedo</h2>

      {gameKey.error && (
        <div>
          <div>
            <i
              className={"fas fa-exclamation-triangle"}
              style={{ marginRight: "10px" }}
            />
            Geen spel data gevonden.
          </div>
          Error: {gameKey.error}
        </div>
      )}
      {gameKey.processing && <div>Loading...</div>}

      {!gameKey.processing && (
        <div className="cluedo-start" onClick={handleNewGame}>
          <div className="cluedo-start-img"></div>
          <p>Nieuw Spel</p>
        </div>
      )}
    </div>
  );
};

export default CluedoStart;
