const TextInput = ({ label, name, value, placeholder, onChange }) => {
  return (
    <div className={"form-group"}>
      <label htmlFor={name}>{label}</label>
      <input
        name={name}
        type="text"
        value={value}
        placeholder={placeholder}
        onChange={(e) => onChange(e.target.value)}
      />
    </div>
  );
};

export default TextInput;
