const ClueDetail = ({ clue }) => (
  <div className="clue-detail">
    <div className="clue-detail-img-wrapper">
      <img
        className="clue-detail-img"
        src={process.env.REACT_APP_BASE_URL + clue.image}
        title={clue.title}
        alt={"clue afbeelding"}
      />
    </div>
    <table className="detail-table">
      <tbody>
        <tr className="detail-row">
          <th>Titel: </th>
          <td>{clue.title}</td>
        </tr>
        <tr className="detail-row">
          <th>Type: </th>
          <td>
            {
              {
                suspect: <>Verdachte</>,
                weapon: <>Wapen</>,
                room: <>Kamer</>,
              }[clue.type]
            }
          </td>
        </tr>
        {clue.description !== "" && (
          <tr className="detail-row description">
            <th>Beschrijving: </th>
            <td>{clue.description}</td>
          </tr>
        )}
      </tbody>
    </table>
  </div>
);

export default ClueDetail;
