import LogEntry from "./LogEntry";

const Log = ({ logEntries }) => {
  const backlog = logEntries.filter((logEntry) => logEntry !== logEntries[0]);
  const oldEntry = backlog.map((logEntry, index) => (
    <LogEntry key={index} entry={logEntry} />
  ));

  return (
    <div className="log-container">
        <h3 className="log-header">
            <span>Antwoord historiek</span>
            <span>Gegeven antwoorden: {logEntries.length}</span>
        </h3>
        {logEntries.length > 0 && <h4 style={{ textAlign: 'left', marginTop: '10px' }}>Meest recente suggestie:</h4>}
      <div className="log-new">
        {logEntries[0] ? (
          <LogEntry entry={logEntries[0]} />
        ) : (
          <div className={"entry"}>Maak suggesties en ontrafel de moord</div>
        )}
      </div>
        {oldEntry.length > 0 && <h4 style={{ textAlign: 'left', marginTop: '10px' }}>Oudere suggesties:</h4>}
      <div className="log-old">{oldEntry}</div>
    </div>
  );
};

export default Log;
