import React, { useContext } from "react";
import {
  FaRegCheckCircle,
  FaRegQuestionCircle,
  FaRegTimesCircle,
} from "react-icons/fa";
import { getClueById } from "../../Helpers/ClueById";
import { CluesContext } from "../Main";

const LogEntry = ({ entry }) => {
  const { data } = useContext(CluesContext);

  const weapon = getClueById(data, entry.suggested.weapon);
  const suspect = getClueById(data, entry.suggested.suspect);
  const room = getClueById(data, entry.suggested.room);
  const response = getClueById(data, entry.response.incorrect);

  return (
    <div>
      {suspect && (
        <div className={"entry"}>
          <div>
            <FaRegQuestionCircle />
            <span style={{ marginLeft: "10px", fontWeight: "600" }}>
              {suspect.title} met {weapon.title} in de {room.title}
            </span>
          </div>
          {response ? (
            <h3>
              <FaRegTimesCircle
                color={"#ff0000"}
                className={"crossed"}
                style={{ marginRight: "10px" }}
              />
              <span>{response.title}</span>
            </h3>
          ) : (
            <h3>
              <FaRegCheckCircle
                color={"#14bd14"}
                className={"crossed"}
                style={{ marginRight: "10px" }}
              />
            </h3>
          )}
        </div>
      )}
    </div>
  );
};

export default LogEntry;
