import { Switch } from "react-router";
import React, { useState } from "react";
import CluedoActive from "./CluedoActive";
import GameOver from "./CluedoEnd";
import CluedoStart from "./CluedoStart";
import { NoBaseUrlMessage } from "../NoBaseUrlMessage";
import { useSettings } from "../context/useSettings";

const Cluedo = () => {
  const { settings } = useSettings();
  const [gameKey, setKey] = useState(null);
  const [gameOver, setGameOver] = useState(null);

  const endGame = () => {
    setKey(null);
    setGameOver(null);
  };

  return settings ? (
    <Switch>
      {!gameKey && <CluedoStart onStart={setKey} />}
      {gameOver && <GameOver onEndGame={endGame} outcome={gameOver} />}
      <CluedoActive
        gameKey={gameKey}
        onArrest={setGameOver}
        onEndGame={endGame}
      />
    </Switch>
  ) : (
    <NoBaseUrlMessage />
  );
};

export default Cluedo;
