import React, { useCallback, useEffect } from "react";
import Menu from "./Menu";
import Checklist from "./Checklist";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Intro from "./Intro";
import Cluedo from "./Cluedo/Cluedo";
import Clues from "./Clues/Clues";
import ApiForm from "./Forms/ApiForm";
import { useCookies } from "react-cookie";
import useFetch from "../Hooks/Fetch";
import { useSettings } from "./context/useSettings";

export const CluesContext = React.createContext();

const defaultApiSettings = {
  baseURL: "",
  url: {
    new: process.env.REACT_APP_URL_NEW,
    clues: process.env.REACT_APP_URL_CLUES,
    suggest: process.env.REACT_APP_URL_SUGGEST,
    accuse: process.env.REACT_APP_URL_ACCUSE,
  },
  auth: {
    username: process.env.REACT_APP_USERNAME,
    password: process.env.REACT_APP_PASSWORD,
  },
  clueTypes: {
    suspect: process.env.REACT_APP_SUSPECT,
    weapon: process.env.REACT_APP_WEAPON,
    room: process.env.REACT_APP_ROOM,
  },
};

export const Main = () => {
  const { settings, setSettings } = useSettings();
  const [cookies] = useCookies(["htf-settings"]);
  const { response: clues, doFetch } = useFetch();
  const cookie = cookies["htf-settings"];

  const handleApiFormUpdate = useCallback((baseURL) => {
    setSettings({ ...defaultApiSettings, baseURL });
  }, [setSettings]);

  useEffect(() => {
    if (settings) {
      doFetch(settings.url.clues, { method: "GET" }).then();
    }
  }, [settings, doFetch]);

  useEffect(() => {
    if (cookie) handleApiFormUpdate(cookie);
  }, [cookie, handleApiFormUpdate]);

  return (
    <Router>
      <div className="App">
        <h1>Hack The Future</h1>
        <div className={"panel-1"}>
          <Menu />
          <Checklist />
        </div>
        <div className={"panel-2"}>
          <CluesContext.Provider value={clues}>
            <Switch>
              <Route path={["/", "/intro"]} exact render={() => <Intro />} />
              <Route path="/cluedo" render={() => <Cluedo />} />
              <Route path="/clues" render={() => <Clues />} />
              <Route
                path="/settings"
                render={() => <ApiForm onUpdate={handleApiFormUpdate} />}
              />
            </Switch>
          </CluesContext.Provider>
        </div>
      </div>
    </Router>
  );
};
