import React, { useContext } from "react";
import Button from "../Button";
import { getClueById } from "../../Helpers/ClueById";
import { CluesContext } from "../Main";

const GameOver = ({ onEndGame, outcome }) => {
  const { data } = useContext(CluesContext);

  const gameOutcome = outcome.response;
  const accusation = outcome.response.accusation;
  const solution = outcome.response.solution;

  return gameOutcome && accusation && solution ? (
    <div className={"full file"}>
      {gameOutcome.correct && (
        <div className={"outcome"} style={{ display: "block" }}>
          <h2> Zaak Opgelost!</h2>
          <p>{gameOutcome.message}</p>
          <img className={"qr-box"} src={gameOutcome.easterEgg} alt="qr code" />
        </div>
      )}

      {!gameOutcome.correct && (
        <div className={"outcome"}>
          <h2>Helaas!</h2>
          <p>{gameOutcome.message}</p>

          <div className="outcome-data">
            <h3>Jouw antwoord</h3>
            <div className="text">
              {`${getClueById(data, accusation.suspect).title} met de ${
                getClueById(data, accusation.weapon).title
              } in de ${getClueById(data, accusation.room).title}`}
            </div>
            <div className="outcome-images">
              <img
                src={
                  process.env.REACT_APP_BASE_URL +
                  getClueById(data, accusation.suspect).image
                }
                alt={getClueById(data, accusation.suspect).title}
              />
              <img
                src={
                  process.env.REACT_APP_BASE_URL +
                  getClueById(data, accusation.weapon).image
                }
                alt={getClueById(data, accusation.weapon).title}
              />
              <img
                src={
                  process.env.REACT_APP_BASE_URL +
                  getClueById(data, accusation.room).image
                }
                alt={getClueById(data, accusation.room).title}
              />
            </div>
          </div>

          <div className="outcome-data">
            <h3>Oplossing</h3>
            <div className="text">
              {`${getClueById(data, solution.suspect).title} met de ${
                getClueById(data, solution.weapon).title
              } in de ${getClueById(data, solution.room).title}`}
            </div>
            <div className="outcome-images">
              <img
                src={
                  process.env.REACT_APP_BASE_URL +
                  getClueById(data, solution.suspect).image
                }
                alt={getClueById(data, solution.suspect).title}
              />
              <img
                src={
                  process.env.REACT_APP_BASE_URL +
                  getClueById(data, solution.weapon).image
                }
                alt={getClueById(data, solution.weapon).title}
              />
              <img
                src={
                  process.env.REACT_APP_BASE_URL +
                  getClueById(data, solution.room).image
                }
                alt={getClueById(data, solution.room).title}
              />
            </div>
          </div>
        </div>
      )}

      <Button onClick={() => onEndGame()} value={"Nieuw Spel"} />
    </div>
  ) : null;
};

export default GameOver;
