import React, { useCallback, useState } from "react";
import CluedoForm from "../Forms/CluedoForm";
import Log from "./Log";
import Button from "../Button";
import { Mansion } from "./Mansion";
import Notebook from "../Notebook/Notebook";
import { useSettings } from "../context/useSettings";

const CluedoActive = ({ gameKey, onArrest, onEndGame }) => {
  const { settings } = useSettings();
  const [log, setLog] = useState([]);
  const [room, setRoom] = useState(null);
  const [excluded, setExcluded] = useState([]);

  const [isArrest, setIsArrest] = useState(false);

  const suggestUrl = settings.url.suggest + "?key=" + gameKey;
  const arrestUrl = settings.url.accuse + "?key=" + gameKey;

  const onClick = () => {
    if (window.confirm("Ben je zeker dat je dit spel wil beëindigen?")) {
      onEndGame();
    }
  };

  const onToggle = (id) =>
    setExcluded(
      !excluded.includes(id)
        ? [...excluded, id]
        : excluded.filter((a) => a !== id)
    );

  const updateAfterSuggest = useCallback(
    (logUpdate) => {
      const updatedList = [logUpdate.response.incorrect, ...excluded];
      setExcluded([...new Set(updatedList)]);
      setLog([logUpdate, ...log]);
    },
    [excluded, log]
  );

  return (
    <div className={"file full"}>
      {room && (
        <CluedoForm
          title={room.title}
          url={suggestUrl}
          roomId={room.id}
          onSubmit={updateAfterSuggest}
          onClose={() => setRoom(null)}
        />
      )}
      {isArrest && (
        <CluedoForm
          title={"Arrest"}
          url={arrestUrl}
          onSubmit={onArrest}
          onClose={() => setIsArrest(null)}
        />
      )}

      {!room && !isArrest && (
        <div>
          <h2>Cluedo</h2>
          <Mansion onClick={setRoom} />
          <div className="cluedo-btn-box">
            <Button value="Maak arrestatie" onClick={() => setIsArrest(true)} />
            <Button value={"Spel Stoppen"} onClick={() => onClick()} />
          </div>
        </div>
      )}
      <Log logEntries={log} />
      <Notebook disproved={excluded} onClick={onToggle} />
    </div>
  );
};

export default CluedoActive;
